@use "global" as *;

/* Loading*/
.splash{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  background: #D6E6F5; /*  */
  text-align: center;
  color: #fff;
  display: flex; /*  */
  justify-content: center;
  align-items: center;
}


/* Loading */
.splash__logo img {
  width:rem(260);
}

/* fadeUp */
.fade-up{
  animation: fade-up 0.5s ease-out forwards;
	opacity: 0;
}

@keyframes fade-up{
  0% {
  opacity: 0;
  transform: translateY(100px);
  }

  100% {
  opacity: 1;
  transform: translateY(0);
  }
}
